html,body {
    margin: 0;
    padding: 0;
}

th {
    padding: 15px;
}
td {
    padding: 15px;
    vertical-align: top;
}

body {
    /* Smoothing */
    text-rendering: optimizeLegibility;
    -moz-osx-font-smoothing: grayscale;
    font-smoothing: antialiased;
    -webkit-font-smoothing: antialiased;
    text-shadow: rgba(0, 0, 0, .01) 0 0 1px;
}




